/** css初始化，可复制到任何网站中使用，可不在此页面添加网站主观元素 */
* {
  font-family: Microsoft YaHei, Yahei, "Helvetica Neue", Helvetica, Arial, Segoe UI, Roboto, Hiragino Sans GB, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* width and height are not affected by their borders or padding. */
}
html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  background-color: #fff;
}
.g-theme ::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
.g-theme ::-webkit-scrollbar-track {
  background: #dadce8;
}
.g-theme ::-webkit-scrollbar-thumb {
  border-radius: 100px;
  background: #b6bad1;
}
.g-theme a {
  transition: none;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
.g-theme a:hover {
  color: inherit;
}
.g-theme a:active,
.g-theme a:link,
.g-theme a:visited {
  text-decoration: none;
}
.g-theme h1,
.g-theme h2,
.g-theme h3,
.g-theme h4,
.g-theme h5,
.g-theme p {
  color: inherit;
}
.g-theme input {
  border: none;
  outline: none;
}
.g-theme button {
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}
.g-full-screen {
  width: 100vw;
  height: 100vh;
}
.g-no-scroll {
  overflow: hidden;
}
.g-flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.g-flex-center-column {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.g-flex-between {
  display: flex;
  justify-content: space-between;
}
.g-all-transition {
  transition: all 0.3s;
}
.g-theme a {
  color: #1890ff;
}
.g-theme a:hover {
  color: #0069cb;
}
.g-theme .content {
  width: 1040px;
  margin: 0 auto;
}
.g-theme strong {
  background-color: #feeff0;
  border-radius: 4px;
  color: #f5222d;
  font-weight: normal;
}
